import React, {Component} from "react"

class Toggle extends Component {
    constructor(props) {
        super(props)

        this.state = {
            isDark: true,
        }
    }

    render() { 
        return <button onClick={() => this.setState({isDark: !this.state.isDark})}>{this.state.isDark ? "Hello" : "World"}</button>
    }



}

export default Toggle